.animated-grid {

    height: 85vh;

    display: grid;
    grid-gap: 1rem;
    gap: 1rem;

    /* Explicit grid */
    grid-template-areas: 'a b c d' 'l 🌟 🌟 e' 'k 🌟 🌟 f' 'j i h g';

    grid-template-rows: repeat(4, 20%);
    grid-template-columns: auto auto auto auto;

    --stagger-delay: 50ms;
}


@-webkit-keyframes cardEntrance {
    from {
        opacity: 0;
        transform: scale(0.1);
        -webkit-filter: hue-rotate(180deg);
                filter: hue-rotate(180deg);
    }
    to {
        opacity: 1;
        transform: scale(1);
        -webkit-filter: hue-rotate(0deg);
                filter: hue-rotate(0deg);
    }
}


@keyframes cardEntrance {
    from {
        opacity: 0;
        transform: scale(0.1);
        -webkit-filter: hue-rotate(180deg);
                filter: hue-rotate(180deg);
    }
    to {
        opacity: 1;
        transform: scale(1);
        -webkit-filter: hue-rotate(0deg);
                filter: hue-rotate(0deg);
    }
}

.card {

    overflow: hidden;
    text-align: center;
    padding: inherit;
    -webkit-animation: cardEntrance 500ms ease-out;
            animation: cardEntrance 500ms ease-out;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
    box-shadow: 0 2px 9px 5px rgba(41, 41, 40, 1);
    border-style: groove;
    border-color: black;
}

.card:active {
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(28,28,171,1) 35%, rgba(146,237,255,1) 100%);
  
  box-shadow: 0 5px rgba(41, 41, 40, 1);
  transform: translateY(4px);
}


